.news-card-all-container {
    display: flex;
    width: 100%;
    margin-top : 2em
}

.news-card-all-image {
    width: 100%;
    position: relative;
    padding-top: 66.66%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.news-card-all-inner-text {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
}
.news-card-all-date {
    font-size: 0.9em;
}
.fa-calendar-alt {
    font-size:1em!important;
    margin-right: 0.5em!important;
}
.news-card-all-button{
    border: none;
    padding: 0.75em;
    font-size: 1em;
    font-weight: bold;
    background-color: var(--main-bg-color);
    color: white;
}
.news-card-all-left {
    width : 35%;
    min-width: 300px;
}
.news-card-all-right {
    margin-left: 1em;
    width: 65%;
}

@media screen and (max-width: 768px){
    .news-card-all-container {
        flex-direction: column;
    }
    .news-card-all-left {
        width: 100%;
    }
    .news-card-all-right {
        margin-left: 0;
        width: 100%;
    }
}