.paginate-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.paginate-element {
    color: black;
    padding: 0.5em;
    font-size: 1em;
    border: 1px solid rgba(0,0,0,0.1);
    margin-left: 0.1em;
    margin-right: 0.1em;
    width: 1em;
    display: flex;
    justify-content: center;
}
.paginate-element:hover{
    cursor: pointer;
}
.paginate-inactive {
    opacity: 0.5;
}
.paginate-inactive:hover {
    cursor: unset!important;
    pointer-events: none;
}