.template-news-container {
    display: flex;
}
.template-news-left{
    width: 100%;
}
.template-news-right {
    padding-right: 2em;
    box-sizing: border-box;
}
.template-new-left-menu {
    border: 1px solid rgba(0,0,0,0.1);
    width: 270px;
}
.template-new-left-title {
    border : 1px solid rgba(0,0,0,0.1);
    border-bottom: none;
    margin: 0;
    padding: 0.75em;
    background-color: var(--main-bg-color);
    color: white;
}

@media screen and (max-width: 1280px){
    .template-news-right {
        display: none;
    }
}